import back from '../../components/back.vue';
export default {
	data() {
		return {
			pageNameFlag:1,
			showFlag:0,
			pageName:'忘记密码',
			phoneNum:'',
			passwordFlag:true,
			againFlag:true,
			password:'',
			again:'',
			seeFlag:0,
			seeAgainFlag:0,
			loadingFlag:false,
			doneFlag:0,
			sendAgain:'发送验证码',
			sendAgainFlag:0,
			resetFlag:0,
			vercode:'',
			second:60,
			errorMsg:''
		}
	},
	methods: {
		changeVercode(e){
			var that = this;
			if(e.detail.value.length>0){
				that.vercode=e.detail.value;
				that.resetFlag=1;
			}else{
				that.vercode='';
				that.resetFlag=0;
			}
			if(e.detail.value.length==6){
				that.nextFlag=1
			}else{
				that.nextFlag=0
			}
		},
		reset(){
			this.vercode='';
			this.resetFlag=0;
			this.nextFlag=0
		},
		setPassword(e){
			var that = this;
			if(e.detail.value.length>0){
				that.seeFlag=1;
				that.password=e.detail.value;
				if(e.detail.value.length>6&&that.again.length>6){
					that.doneFlag=1;
				}else{
					that.doneFlag=0;
				}
			}else{
				that.seeFlag=0;
				that.password='';
			}
		},
		setAgain(e){
			var that = this;
			if(e.detail.value.length>0){
				that.seeAgainFlag=1;
				that.again=e.detail.value;
				if(e.detail.value.length>6&&that.password.length>6){
					that.doneFlag=1;
				}else{
					that.doneFlag=0;
				}
			}else{
				that.seeAgainFlag=0;
				that.again='';
			}
		},
		see(flag){
			this.passwordFlag=flag;
		},
		seeAgain(flag){
			this.againFlag=flag;
		},
		sendVercodeAgain(){
			var that = this;
			that.sendAgainFlag=1
			uni.request({
			    url: that.serverUrl+'/user/authCode', 
				method:'POST',
			    data: {
			       phone:that.phoneNum,
				   devCode:that.cid(),
				   type:3
			    },
			    header: {
			        'content-type': 'application/x-www-form-urlencoded'
			    },
				sslVerify:false,
			    success: (res) => {
					if(res.data.resultCode=='10000'){
						
					}else{
						that.errorMsg=that.showError(res.data.msgCode,res.data.msg);
						that.showFlag=1
						setTimeout(function() {
							that.errorMsg='';
							that.showFlag=0;
						}, 5000);
					}
			    },
				fail() {
					uni.showModal({
					    title: '提示',
					    content: '网络异常，请检查网络后重试',
						showCancel:false,
					    success: function (res) {
					        if (res.confirm) {
					            console.log('用户点击确定');
					        } else if (res.cancel) {
					            console.log('用户点击取消');
					        }
					    }
					});
				}
			});
			if(that.second!=60){
				return;
			}
			that.second--;
			var intervalID =setInterval(function(){
				that.sendAgain ='重新发送('+that.second-- +"S)";
			},1000);
			setTimeout(function(){
				clearInterval(intervalID);
				that.sendAgain ="发送验证码";
				that.second=60;
				that.sendAgainFlag=0
			},61000)
			
		},
		complete(){
			var that = this;
			var password = that.password;
			var again = that.again;
			if(password.length>=6&&again.length>=6&&password==again&&that.vercode!=''){
				uni.showLoading({
					title:'加载中',
					mask:true,
					success() {
						uni.request({
						    url: that.serverUrl+'/user/resetPsd', 
							method:'POST',
						    data: {
						       phone:that.phoneNum,
							   devCode:that.cid(),
							   password:password,
							   code:that.vercode
						    },
						    header: {
						        'content-type': 'application/x-www-form-urlencoded'
						    },
							sslVerify:false,
						    success: (res) => {
								uni.hideLoading();
								if(res.data.resultCode=='10000'){
									uni.showToast({
									    title: '修改完成',
										icon:'success',
									    duration: 2000,
										mask:true
									});
									setTimeout(function () {
										uni.switchTab({
											url:'../information/information'
										})
									},2000)
								}else{
									that.errorMsg=that.showError(res.data.msgCode,res.data.msg);
									that.showFlag=1
									setTimeout(function() {
										that.errorMsg='';
										that.showFlag=0;
									}, 5000);
								}
						    },
							fail:(re)=>{
								uni.hideLoading();
								uni.showModal({
								    title: '提示',
								    content: '网络异常，请检查网络后重试',
									showCancel:false,
								    success: function (res) {
								        if (res.confirm) {
								            console.log('用户点击确定');
								        } else if (res.cancel) {
								            console.log('用户点击取消');
								        }
								    },
								});
							}
						});
					}
				})
			}else{
				uni.showModal({
					title: '设置密码失败',
					content: '请检查输入',
					success: function (res) {
					    if (res.confirm) {
					        console.log('用户点击确定');
					    } else if (res.cancel) {
					        console.log('用户点击取消');
					    }
					},
				})
			}
		}
	},
	onLoad(e) {
		this.phoneNum=e.phoneNum;
	},
	components:{
		back
	}
}
